@import url(../config.css);

.container {
  max-width: 1024px;
  margin: 0 auto;
}
.wraper {
  display: flex;
}
.wraper-space-between {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.content {
  flex-grow: 1;
}
.navigation {
  height: 420px;
  width: 350px;
  margin-right: 30px;
  padding: 20px;
  position: sticky;
  top: 40px;
  border: 1px solid #333;
  border-radius: 4px;
}

body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background: #fff;
  color: #333;
  font-weight: 100;
}

header {
  padding: 40px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333;
}
h1 {
  font-size: 32px;
}
h2 {
  margin-bottom: 20px;
  text-align: center;
  text-transform: uppercase;
}
h3 {
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 20px;
}
a {
  text-decoration: none;
  color: #333;
}
a:hover {
  background: #eee;
}
hr {
  margin: 20px 0;
  width: 100%;
}
.up {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  position: fixed;
  top: 90%;
  left: 95%;
  background: white;
  text-align: center;
}
.up a {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  line-height: 50px;
}
.up a:hover {
  background: none;
}

.subsection {
  margin-bottom: 15px;
}
